<template>
  <div style="background: rgba(0, 0, 0, 0.36)">
    <Card style="width:100%;background: rgba(0, 0, 0, 0.36);border: none;border-bottom: white 1px solid">
      <Icon type="ios-bulb"/>
      
      <span style="color: #ffabfa"></span>
    </Card>
    <div  id="allmap" ref="allmap" style="width: 100%;" :style="{height:clientHeight+150+'px'}"></div>
  </div>
</template>

<script>
  export default {
    name: "monitor",
    props:['clientHeight'],
    data() {
      return {
      }
    },
    methods:{
      map(){
        let map =new BMap.Map(this.$refs.allmap);// Map
        map.centerAndZoom(new BMap.Point(116.404, 39.915), 11);// ,
        map.addControl(new BMap.MapTypeControl({//
          mapTypes:[
            BMAP_NORMAL_MAP,
            BMAP_HYBRID_MAP
          ]}));
        map.setMapStyle({style:'dark'});
        map.setCurrentCity("");//  
        map.enableScrollWheelZoom(true);//
      },
      getLocation(){
        let geolocation = new BMap.Geolocation();
        geolocation.getCurrentPosition(function(r){
          if(this.getStatus() == BMAP_STATUS_SUCCESS){
            // var mk = new BMap.Marker(r.point);
            // map.addOverlay(mk);
            // map.panTo(r.point);
           console.log(r.address.city)
          }
          else {
            alert('failed'+this.getStatus());
          }
        },{enableHighAccuracy: true})
      }
    },
    mounted() {
      this.map()
      this.getLocation()
    }
  }
</script>

<style scoped>

</style>



